import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import styled from "styled-components";
import './Details.css';
import Modal from 'react-modal';
import { useDispatch, useSelector } from "react-redux";
import { addItem } from "./../store.js";



// let YellowBtn = styled.button`
// background : yellow;
// color : black;
// padding : 10px;
// `

function Details(props) {

    let [fade, setFade] = useState('');
    const navigate = useNavigate(0);
    let { id } = useParams();
    let findProducts = props.data2.find((x) => x.id == id);
    let state = useSelector((state) => state.cart);

    let dispatch = useDispatch()

    useEffect(() => {
        setFade('end')
        console.log(state)
        return () => {
            setFade('');
        }
    })

    useEffect(()=>{
        let getWatched = localStorage.getItem('watched')       
        getWatched = JSON.parse(getWatched)
        getWatched.push(findProducts.id)
        localStorage.setItem('watched', JSON.stringify(getWatched))
    }, [])

    return (
        <>
            <div className='main'>
                <Modal isOpen={true} className={'start ' + fade}>
                    <button onClick={() => { navigate('/cart') }}>Back</button>
                    <img src={findProducts.img} height='300px' width='auto'></img>
                    <h4>{findProducts.title}</h4>
                    <p>{findProducts.price}</p>
                    <button onClick={() => {                        
                        dispatch(addItem({ id: 3, name: 'PRS Mc594', count: 13 }))                       
                    }}>주문하기</button> <span>카드혜택(무이자16개월)</span>
                </Modal>
            </div>
        </>
    )
}



export default Details;